.main-nav {
  width: @mobile-width;

  @media (min-width: @tablet-width) {
    width: @tablet-width;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
  }
}

.main-nav__wrapper {
  position: relative;
}

.main-nav__menu {

  display: none;
  position: relative;
  width: 100%;
  z-index: 1;

  @media (min-width: @tablet-width) {
    display: block;
    z-index: 0;
  }
}

.main-nav__open-menu {
  width: 23px;
  height: 22px;

  border: none;

  position: absolute;
  top: 17px;
  right: 20px;

  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-nav__close-menu {
  width: 22px;
  height: 22px;

  border: none;

  position: absolute;
  top: 17px;
  right: 16px;
  z-index: 1;

  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.menu--open {
  display: block;
}

.main-nav--closed {
  display: none;
}

.header--no-js {
  .open-menu,
  .close-menu {
    display: none;
  }

  .menu {
    display: block;
    position: static;
  }
}
