.form-back {
  width: @mobile-width;

  height: 158px;
  background-image: url("../img/form-back-mobile.jpg");
  background-color: @black;
  position: relative;

  @media (min-width: @tablet-width) {
    min-width: @tablet-width;
    height: 290px;
    background-image: url("../img/form-back-tablet.jpg");
    background-color: @black;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    height: 300px;
    background-image: url("../img/form-back-desktop.jpg");
    background-color: @black;
    background-position: right;
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/back-desktop@2x.jpg");
    background-size: 1338px auto;
  }


  &::after {

    position: absolute;
    content: "";
    background-image: url("../img/intro-triangle.svg");
    height: 57px;
    width: 320px;
    bottom: 0;
    left: 0;

    @media (min-width: @tablet-width) {

      position: absolute;
      content: "";
      background-image: url("../img/intro-triangle-tablet.svg");
      height: 57px;
      width: 768px;
      bottom: 0;
    }

    @media (min-width: @desktop-width) {
      position: absolute;
      content: "";
      background-image: url("../img/intro-triangle-desktop.svg");
      height: 57px;
      width: @desktop-width;
      bottom: 0;
    }
  }
}

.form__welcome {
  display: block;
  transform: translate(20%, 130%);

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.form__welcome-tablet {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    transform: translate(40%, 120%);
  }

  @media (min-width: @desktop-width) {
    display: block;
    transform: translate(80%, 120%);
  }
}
