.name-data {
  width: @mobile-width;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  @media (min-width: @tablet-width) {
    width: 360px;
  }

  @media (min-width: @desktop-width) {
    width: 600px;
  }

  &::before {
    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      background-image: url("../img/abc.svg");
      height: 1px;
      width: 320px;
      top: 175px;
      right: -170px;

      transform: rotate(90deg);
    }
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/photo-divider.svg");
    height: 1px;
    width: 320px;
    bottom: -25px;
    left: 0;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

.name-data__wrapper {
  @media (min-width: @desktop-width) {
    margin-left: 80px;
  }
}

.name-data__title {

  .bigText();

  text-align: center;
  text-transform: uppercase;

  margin: 0;
  margin-top: 39px;

  @media (min-width: @tablet-width) {
    margin-bottom: 28px;
    margin-top: 0;
  }
}

.name-data__description {

  .smallText();

  text-transform: uppercase;

  margin: 0;
  margin-left: 15px;
}

.name-data__name,
.name-data__surname {
  min-height: 58px;

  border: none;
  background-color: #f2f2f2;
  padding: 16px;

  margin-bottom: 16px;
}

.name-data__second-name {
  min-height: 58px;

  border: none;
  background-color: #f2f2f2;
  padding: 16px;
}


.grid-template-2 {
  @media (min-width: @tablet-width) {
    width: @tablet-width;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.name-data__name,
.name-data__surname,
.name-data__second-name {
  width: 280px;
  color: @black;

  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;

  &:hover,
  &:focus {
    background-color: #e5e5e5;
  }

  &:active {
    background-color: #ffffff;
    border: none;
  }

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}
