.features-highlighted {
  @media (min-width: @tablet-width) {

    width: 778px;
    min-height: 256px;
    display: flex;
    margin: 0;
    margin-top: 56px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    margin-top: 56px;
    width: 1200px;
  }
}

.features-highlighted__title {

  color: @white;

  text-align: center;
  text-transform: uppercase;

  .bigText();

  margin: 0;
  margin-top: 33px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.features-highlighted__img {

  width: 320px;
  height: 260px;
  background-color: #000000;

  @media (min-width: @tablet-width) {
    width: 512px;
    height: 256px;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
  }
}

.features-highlighted__num {
  color: @white;

  .smallText();

  text-align: center;

  margin: 0;
  margin-top: 11px;

  @media (min-width: @tablet-width) {
    margin-top: 17px;
  }
}

.features-highlighted__text {
  color: @white;

  .smallText();

  text-align: center;
  text-transform: uppercase;

  margin: 0;
  margin-top: 17px;

  @media (min-width: @tablet-width) {

    margin-top: 17px;
  }
}

.block {
  background-color: @blue;
  width: @mobile-width;
  min-height: 211px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 356px;
    height: 256px;

    padding-left: 33px;
    padding-right: 33px;
  }

  @media (min-width: @desktop-width) {
    width: 400px;
  }
}


.features-highlighted__br-mobile {
  display: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.features-highlighted__br-tablet {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}

.features-highlighted__br-desktop {
  @media (min-width: @tablet-width) {
    display: none;
  }
}
