.places {
  margin-top: 59px;
  position: relative;

  @media (min-width: @tablet-width) {
    width: @tablet-width;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 43px;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
  }

  &::before {
    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      background-image: url("../img/small-divider.svg");
      height: 1px;
      width: 123px;
      top: 30px;
      right: 60px;
    }

    @media (min-width: @desktop-width) {
      width: 280px;
    }
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/photo-divider.svg");
    height: 1px;
    width: 320px;
    bottom: -25px;
    left: 0;

    @media (min-width: @tablet-width) {
      position: absolute;
      content: "";
      background-image: url("../img/small-divider.svg");
      height: 1px;
      width: 123px;
      top: 30px;
      left: 60px;
    }

    @media (min-width: @desktop-width) {
      width: 280px;
    }
  }
}

.places__title {

  .bigText();

  text-align: center;
  text-transform: uppercase;

  margin-bottom: 26px;

  @media (min-width: @desktop-width) {
    margin-bottom: 59px;
  }
}

.places__legend {

  .bigText();

  text-transform: capitalize;
}

.places__list {
  margin: 0;

  @media (min-width: @tablet-width) {
    width: 480px;

    column-count: 2;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
  }
}

.places__item {
  margin-bottom: 16px;

  @media (min-width: @tablet-width) {
    width: 240px;
  }
}


.places__control {

  position: relative;
  display: block;
  padding-left: 42px;
  padding-bottom: 10px;
}

.places__control-input[type="radio"] + .places__control-mark {
  border-radius: 50%;
}


.places__control-mark {
  position: absolute;

  left: -20px;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;

  &:hover,
  &:focus {
    background-color: #e5e5e5;
  }

  &:active {
    background-color: #d5d5d5;
  }
}

.places__control-input[type="checkbox"]:checked + .places__control-mark::before,
.places__control-input[type="checkbox"]:checked + .places__control-mark::after {
  position: absolute;
  top: 15px;
  left: 12px;
  width: 22px;
  height: 16px;
  background-image: url("../img/tick.svg");
  content: "";
}
