.photos {
  width: @mobile-width;

  @media (min-width: @tablet-width) {
    width: @tablet-width;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
  }
}

.photos__list {
  margin: 0;
  padding: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  // @media (min-width: @desktop-width) {

  // }
}

.photo-card {
  position: relative;
  margin-top: 55px;

  @media (min-width: @tablet-width) {

    margin-top: 0;
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/photo-divider.svg");
    height: 1px;
    width: 320px;
    bottom: -25px;
    left: -20px;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

.photos-list:first-child {

  margin-top: 24px;
}

.photo-card__img {
  display: block;
}

.photo-card__block-creator {
  @media (min-width: @tablet-width) {
    background-color: #f2f2f2;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    background-color: rgba(0, 0, 0, 0.3);

    width: 100%;

    position: absolute;
    bottom: 0;
  }
}

.photo-card__block-creator--big {
  @media (min-width: @tablet-width) {
    background-color: #f2f2f2;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    background-color: rgba(0, 0, 0, 0.3);

    width: 100%;

    position: absolute;
    bottom: 0;
  }
}

.photo-card__title {
  color: @black;

  .bigText();

  text-transform: uppercase;

  margin-bottom: 5px;

  @media (min-width: @tablet-width) {
    margin-top: 0;
    margin-left: 20px;
    font-size: 14px;
    line-height: 21px;
    padding-top: 22px;
  }

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__author {
  color: @dark-grey;

  .smallText();

  text-transform: capitalize;

  margin: 0;

  @media (min-width: @tablet-width) {
    padding-bottom: 15px;
    margin-left: 20px;
  }

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__block-big {
  display: flex;
  gap: 14px;
  padding-top: 11px;

  @media (min-width: @tablet-width) {
    min-height: 25px;
    margin-right: 20px;
  }
}

.photo-card__block-small {
  display: flex;
  gap: 14px;
  padding-top: 11px;

  @media (min-width: @tablet-width) {
    min-height: 25px;
    margin-right: 20px;
  }
}

.photo-card__number-likes {
  color: @black;

  .bigText();

  margin: 0;

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.photo-card__icon-like {
  fill: @black;
  opacity: 0.3;

  @media (min-width: @desktop-width) {
    opacity: 1;
    fill: @white;
  }
}
